.userbox {
  z-index: 15;
  position: relative;
  background: #ffffff;
  width: 700px;
  border-radius: 4px;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  margin: 100px auto 10px;
  overflow: hidden;

  padding: 40px;
}
.userdata {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.userdata ul {
  list-style: none;
}
.userdata ul li {
  padding: 10px 10px;
  font-weight: bold;
}
.userdata ul li .logout {
  color: #0d6fca;
  font-size: 16px;
  cursor: pointer;
  /* position: absolute; */
  font-weight: bold;
  display: block;
  text-decoration: none;
}
